<template>
    <div class="nav">
        <button @click="$emit('left')">{{leftData}}</button>
        <h1>{{title}}</h1>
        <button @click="$emit('right')">{{rightData}}</button>
    </div>
</template>

<script>
export default{
    props:{
        leftData:{
            type:String,
            required:true,
        },
        rightData:{
            type:String,
            required:true,
        },
        title:{
            type:String,
            required:true,
        },
    }
}
</script>

<style scoped>
    .nav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        border: 5px solid #99CCCC;
        background-color: #FFFFCC;
    }
    button{
        margin: 0 100px;
        padding: 10px 20px;
        background-color: #CCCCFF;
    }
</style>
