import Vue from 'vue'
import App from './App.vue'
import router from './router'
import NavBar from './components/navBar'
Vue.config.productionTip = false


Vue.use(function(Vue){
  Vue.component('NavBar',NavBar)
})

import QFUI from '@/qf-ui'
Vue.use(QFUI) 

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
